import { useState, useRef } from "react"
import { motion, AnimatePresence, useMotionValue, useSpring, useTransform } from "framer-motion"
import { MapPin, Sparkles, Navigation, Clock, Briefcase, IndianRupee } from "lucide-react"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"

const MOCK_JOBS = [
  {
    id: 1,
    title: "Skilled Mason",
    location: "Whitefield, Bangalore",
    area: "Whitefield",
    pay: "850 - 950",
    match: 94,
    type: "Daily Wage",
    coordinates: { top: "30%", left: "60%" },
    color: "blue",
  },
  {
    id: 2,
    title: "Painter (Interior)",
    location: "Indiranagar, Bangalore",
    area: "Indiranagar",
    pay: "900 - 1,100",
    match: 88,
    type: "Contract",
    coordinates: { top: "50%", left: "45%" },
    color: "purple",
  },
  {
    id: 3,
    title: "Electrician",
    location: "HSR Layout, Bangalore",
    area: "HSR Layout",
    pay: "1,200 - 1,500",
    match: 97,
    type: "Urgent",
    coordinates: { top: "70%", left: "65%" },
    color: "emerald",
  },
  {
    id: 4,
    title: "Plumber",
    location: "Yelahanka, Bangalore",
    area: "Yelahanka",
    pay: "950 - 1,050",
    match: 91,
    type: "Daily Wage",
    coordinates: { top: "15%", left: "30%" },
    color: "orange",
  },
]

export function AIMatchedJobs() {
  const [selectedJob, setSelectedJob] = useState<number | null>(null)

  // Tilt Interaction
  const ref = useRef<HTMLDivElement>(null)
  const x = useMotionValue(0)
  const y = useMotionValue(0)

  const mouseX = useSpring(x, { stiffness: 300, damping: 30 })
  const mouseY = useSpring(y, { stiffness: 300, damping: 30 })

  const rotateX = useTransform(mouseY, [-0.5, 0.5], [7, -7])
  const rotateY = useTransform(mouseX, [-0.5, 0.5], [-7, 7])

  const handleMouseMove = (e: React.MouseEvent<HTMLDivElement>) => {
    if (!ref.current) return
    const rect = ref.current.getBoundingClientRect()

    // Normalize mouse position from -0.5 to 0.5
    const width = rect.width
    const height = rect.height

    const mouseXFromCenter = e.clientX - rect.left - width / 2
    const mouseYFromCenter = e.clientY - rect.top - height / 2

    x.set(mouseXFromCenter / width)
    y.set(mouseYFromCenter / height)
  }

  const handleMouseLeave = () => {
    x.set(0)
    y.set(0)
  }

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <div className="space-y-1">
          <div className="flex items-center gap-2">
            <Sparkles className="h-5 w-5 text-purple-500 animate-pulse" />
            <h2 className="text-2xl font-bold">AI-Matched Jobs Near You</h2>
          </div>
          <p className="text-muted-foreground">
            Smart recommendations based on your skills and location in Bangalore
          </p>
        </div>
        <Badge variant="outline" className="gap-1.5 py-1.5 px-3 border-purple-500/30 text-purple-400 bg-purple-500/5">
          <span className="relative flex h-2 w-2">
            <span className="animate-ping absolute inline-flex h-full w-full rounded-full bg-purple-400 opacity-75"></span>
            <span className="relative inline-flex rounded-full h-2 w-2 bg-purple-500"></span>
          </span>
          Live Match
        </Badge>
      </div>

      <motion.div
        ref={ref}
        onMouseMove={handleMouseMove}
        onMouseLeave={handleMouseLeave}
        onClick={() => setSelectedJob(null)}
        style={{
          perspective: 1000,
        }}
        className="relative h-[600px] w-full rounded-xl overflow-hidden border border-border/50 shadow-2xl bg-[#0F1115] group cursor-pointer"
      >
        <motion.div
          style={{
            rotateX,
            rotateY,
            transformStyle: "preserve-3d",
          }}
          className="absolute inset-0 w-full h-full"
        >
          {/* MAP BASE LAYER */}
          <div className="absolute inset-[-10%] w-[120%] h-[120%] bg-[#0f1115]">
            <svg className="w-full h-full opacity-30" viewBox="0 0 1000 1000" preserveAspectRatio="none">
              <defs>
                <pattern id="grid" width="40" height="40" patternUnits="userSpaceOnUse">
                  <path d="M 40 0 L 0 0 0 40" fill="none" stroke="rgba(255,255,255,0.05)" strokeWidth="1" />
                </pattern>
                <filter id="glow">
                  <feGaussianBlur stdDeviation="2" result="coloredBlur" />
                  <feMerge>
                    <feMergeNode in="coloredBlur" />
                    <feMergeNode in="SourceGraphic" />
                  </feMerge>
                </filter>
              </defs>

              {/* City Blocks (Dark Areas) */}
              <rect width="100%" height="100%" fill="url(#grid)" />
              <path d="M0 0 L300 0 L350 200 L0 300 Z" fill="rgba(30, 41, 59, 0.2)" />
              <path d="M600 0 L1000 0 L1000 400 L500 300 Z" fill="rgba(30, 41, 59, 0.2)" />
              <path d="M0 600 L400 500 L500 1000 L0 1000 Z" fill="rgba(30, 41, 59, 0.2)" />
              <path d="M700 600 L1000 500 L1000 1000 L600 1000 Z" fill="rgba(30, 41, 59, 0.2)" />

              {/* Major Roads (Thick, Glowing) */}
              <g stroke="rgba(59, 130, 246, 0.2)" strokeWidth="3" fill="none" filter="url(#glow)">
                {/* Ring Road style loops */}
                <path d="M 200,100 Q 500,50 800,200 T 800,800 T 200,800 T 200,100" />
                {/* Cross city roads */}
                <path d="M 100,500 L 900,500" />
                <path d="M 500,100 L 500,900" />
                <path d="M 300,300 L 700,700" />
                <path d="M 700,300 L 300,700" />
              </g>

              {/* Minor Roads */}
              <g stroke="rgba(255, 255, 255, 0.05)" strokeWidth="1" fill="none">
                <path d="M 350,200 L 350,800" />
                <path d="M 650,200 L 650,800" />
                <path d="M 200,350 L 800,350" />
                <path d="M 200,650 L 800,650" />
                <circle cx="500" cy="500" r="150" />
              </g>
            </svg>
          </div>

          {/* Area Labels */}
          <div className="absolute inset-0 pointer-events-none z-10" style={{ transform: "translateZ(20px)" }}>
            <div className="absolute top-[18%] left-[25%] text-xs font-semibold text-white/20 tracking-widest uppercase">Yelahanka</div>
            <div className="absolute top-[15%] left-[50%] text-xs font-semibold text-white/20 tracking-widest uppercase">Hebbal</div>
            <div className="absolute top-[35%] left-[65%] text-xs font-semibold text-white/20 tracking-widest uppercase">Whitefield</div>
            <div className="absolute top-[52%] left-[40%] text-xs font-semibold text-white/20 tracking-widest uppercase">Indiranagar</div>
            <div className="absolute top-[48%] left-[10%] text-xs font-semibold text-white/20 tracking-widest uppercase">Malleshwaram</div>
            <div className="absolute top-[65%] left-[60%] text-xs font-semibold text-white/20 tracking-widest uppercase">HSR Layout</div>
            <div className="absolute top-[75%] left-[30%] text-xs font-semibold text-white/20 tracking-widest uppercase">Jayanagar</div>
            <div className="absolute top-[85%] left-[70%] text-xs font-semibold text-white/20 tracking-widest uppercase">Electronic City</div>
          </div>

          {/* Map Vignette & Fog */}
          <div className="absolute inset-0 bg-gradient-to-tr from-[#0F1115] via-transparent to-[#0F1115] opacity-80 pointer-events-none z-0" />

          {/* USER LOCATION MARKER */}
          <motion.div
            className="absolute z-30"
            style={{ top: "42%", left: "50%", transform: "translateZ(60px) translateX(-50%) translateY(-50%)" }}
            initial={{ opacity: 0, scale: 0 }}
            animate={{ opacity: 1, scale: 1 }}
            transition={{ delay: 1, type: "spring" }}
          >
            <div className="relative flex flex-col items-center">
              {/* Pulsing Rings */}
              <div className="absolute -inset-8 bg-blue-500/20 rounded-full animate-ping" />
              <div className="absolute -inset-4 bg-blue-500/30 rounded-full animate-pulse" />

              {/* Marker Core */}
              <div className="relative h-6 w-6 rounded-full bg-blue-500 border-2 border-white/80 shadow-[0_0_20px_rgba(59,130,246,0.6)] flex items-center justify-center">
                <div className="h-2 w-2 bg-white rounded-full" />
              </div>

              {/* Label */}
              <motion.div
                className="mt-3 px-3 py-1.5 bg-blue-500/90 backdrop-blur-md rounded-lg border border-blue-400/30 shadow-xl"
                initial={{ opacity: 0, y: -10 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ delay: 1.5 }}
              >
                <div className="text-[10px] font-bold text-white uppercase tracking-wider whitespace-nowrap flex items-center gap-1.5">
                  <Navigation className="h-3 w-3 fill-white" />
                  Your Location
                </div>
              </motion.div>

              {/* Pole line to ground */}
              <div className="absolute top-6 left-1/2 w-[1px] h-[60px] bg-gradient-to-b from-blue-500/50 to-transparent -translate-x-1/2 pointer-events-none" />
            </div>
          </motion.div>

          {/* Floating Job Pins */}
          <div className="absolute inset-0 z-20" style={{ transform: "translateZ(50px)" }}>
            {MOCK_JOBS.map((job) => (
              <motion.div
                key={job.id}
                className="absolute"
                style={{ top: job.coordinates.top, left: job.coordinates.left }}
                initial={{ opacity: 0, scale: 0, y: 50 }}
                animate={{ opacity: 1, scale: 1, y: 0 }}
                transition={{
                  type: "spring",
                  stiffness: 260,
                  damping: 20,
                  delay: 0.1 * job.id,
                }}
              >
                <div className="relative group/pin cursor-pointer">
                  {/* Pulse Effect */}
                  <motion.div
                    className={`absolute -inset-6 rounded-full bg-${job.color}-500/20 blur-xl`}
                    animate={{
                      scale: [1, 1.3, 1],
                      opacity: [0.3, 0.6, 0.3],
                    }}
                    transition={{
                      duration: 2.5,
                      repeat: Number.POSITIVE_INFINITY,
                      ease: "easeInOut",
                    }}
                  />

                  {/* Connection Line to Map */}
                  <div className="absolute top-1/2 left-1/2 w-[1px] h-[40px] bg-gradient-to-b from-white/20 to-transparent -translate-x-1/2 translate-y-2 pointer-events-none" />

                  {/* Pin */}
                  <motion.button
                    onClick={(e) => {
                      e.stopPropagation()
                      setSelectedJob(job.id === selectedJob ? null : job.id)
                    }}
                    whileHover={{ scale: 1.1, y: -5 }}
                    whileTap={{ scale: 0.95 }}
                    className={`
                      relative h-10 w-10 rounded-full border border-white/20 
                      bg-[#1a1d26]/80 backdrop-blur-md flex items-center justify-center
                      shadow-[0_10px_20px_rgba(0,0,0,0.5)]
                      transition-all hover:border-${job.color}-500 hover:bg-${job.color}-500/10
                    `}
                  >
                    <MapPin className={`h-5 w-5 text-${job.color}-500`} />
                  </motion.button>

                  {/* AI Match Badge Mini */}
                  <div className="absolute -top-3 -right-3 bg-[#1a1d26] text-[10px] font-bold text-white px-2 py-0.5 rounded-full border border-white/10 shadow-lg block">
                    <span className="text-green-500">{job.match}%</span>
                  </div>

                  {/* Hover Tooltip */}
                  {selectedJob !== job.id && (
                    <motion.div
                      initial={{ opacity: 0, y: 5 }}
                      whileHover={{ opacity: 1, y: 0 }}
                      className="absolute left-1/2 -translate-x-1/2 -bottom-8 whitespace-nowrap px-3 py-1 bg-[#1a1d26] backdrop-blur-md rounded-full text-xs text-white border border-white/10 pointer-events-none opacity-0 group-hover/pin:opacity-100 transition-opacity z-50 shadow-lg"
                    >
                      {job.title} <span className="text-muted-foreground">‚Ä¢</span> ‚Çπ{job.pay}
                    </motion.div>
                  )}
                </div>
              </motion.div>
            ))}
          </div>
        </motion.div>

        {/* Selected Job Card Preview (Overlay) */}
        <AnimatePresence>
          {selectedJob && (
            <div
              className="absolute bottom-6 left-6 right-6 md:left-auto md:right-6 md:w-96 z-50"
              style={{ transform: "translateZ(100px)" }}
              onClick={(e) => e.stopPropagation()}
            >
              {MOCK_JOBS.filter((j) => j.id === selectedJob).map((job) => (
                <motion.div
                  key={job.id}
                  initial={{ opacity: 0, y: 20, scale: 0.95 }}
                  animate={{ opacity: 1, y: 0, scale: 1 }}
                  exit={{ opacity: 0, y: 20, scale: 0.95 }}
                  transition={{ type: "spring", stiffness: 300, damping: 25 }}
                >
                  <Card className="bg-[#13161c]/95 backdrop-blur-xl border-white/10 overflow-hidden shadow-[0_20px_50px_rgba(0,0,0,0.5)]">
                    <div className={`h-1.5 w-full bg-gradient-to-r from-${job.color}-500 via-${job.color}-400 to-${job.color}-500 animate-gradient-x`} />
                    <div className="p-5 space-y-4">

                      <div className="flex justify-between items-start">
                        <div>
                          <Badge variant="secondary" className="mb-2 bg-white/5 hover:bg-white/10 text-muted-foreground font-normal">
                            {job.area}
                          </Badge>
                          <h3 className="font-bold text-xl text-white tracking-tight">{job.title}</h3>
                        </div>
                        <div className="flex flex-col items-end gap-1">
                          <div className="text-xs text-muted-foreground">AI Match</div>
                          <div className="text-lg font-bold text-green-400">{job.match}%</div>
                        </div>
                      </div>

                      <div className="grid grid-cols-2 gap-3">
                        <div className="p-3 rounded-lg bg-white/5 border border-white/5">
                          <div className="text-muted-foreground text-xs mb-1 flex items-center gap-1.5">
                            <IndianRupee className="h-3.5 w-3.5" />
                            Daily Pay
                          </div>
                          <div className="font-bold text-white text-lg">‚Çπ{job.pay}</div>
                        </div>
                        <div className="p-3 rounded-lg bg-white/5 border border-white/5">
                          <div className="text-muted-foreground text-xs mb-1 flex items-center gap-1.5">
                            <Briefcase className="h-3.5 w-3.5" />
                            Work Type
                          </div>
                          <div className="font-medium text-white">{job.type}</div>
                        </div>
                      </div>

                      <div className="flex items-center gap-2 text-sm text-muted-foreground/80 bg-black/20 p-2 rounded">
                        <Clock className="h-4 w-4" />
                        <span>Posted 2 hours ago</span>
                      </div>

                      <div className="pt-2 flex gap-3">
                        <Button
                          className={`flex-1 h-11 bg-${job.color}-600 hover:bg-${job.color}-700 text-white font-medium`}
                        >
                          View Job
                        </Button>
                        <Button
                          variant="outline"
                          className="h-11 w-11 p-0 border-white/10 hover:bg-white/5 hover:text-white"
                          onClick={() => setSelectedJob(null)}
                        >
                          ‚úï
                        </Button>
                      </div>
                    </div>
                  </Card>
                </motion.div>
              ))}
            </div>
          )}
        </AnimatePresence>
      </motion.div>
    </div>
  )
}
